import { DatasourceService } from '../datasource.service';
import { Driver } from './driver';
import { MySqlConnectionDriver } from './my-sql/my-sql-connection.driver';
import { PostgresConnectionDriver } from './postgres/postgres-connection.driver';
import { SqlServerConnectionDriver } from './sql-server/sql-server-connection.driver';
import { MissingDriverError } from './error';

export class DriverFactory {
  public static create(connection: DatasourceService): Driver {
    const { type } = connection.options;
    switch (type) {
      case 'mysql':
        return new MySqlConnectionDriver(connection);
      case 'postgres':
        return new PostgresConnectionDriver(connection);
      case 'mariadb':
        return new MySqlConnectionDriver(connection);
      case 'mssql':
        return new SqlServerConnectionDriver(connection);
      default:
        throw new MissingDriverError(type, [
          'mariadb',
          'mssql',
          'mysql',
          'postgres',
        ]);
    }
  }
}
